export default class RegisterRequest{

    constructor(email, password, range, check, name, appat, apmat){
        this.email = email;
        this.password = password;
        this.range = range;
        this.check_privacy = check;
        this.name = name;
        this.appat = appat;
        this.apmat = apmat;
    }

    toJSON(){
        let privacy = this.check_privacy == true ? 1 : 0;
        return {
            email: this.email,
            password: this.password,
            range: this.range,
            check_privacy: privacy,
            name: this.name,
            appat: this.appat,
            apmat: this.apmat
        }
    }
}