<template>
  <v-content>
    <v-container fluid="fluid" fill-height="fill-height">
      <v-layout align-center="align-center" justify-center="justify-center">
        <v-flex xs12="xs12" sm12="sm12" md6>
          <v-card>
            <v-toolbar class="elevation-0" dark="dark" color="red darken-2">
              <v-toolbar-title>Registro</v-toolbar-title>
            </v-toolbar>
            <v-form ref="register_form" v-model="isValid" lazy-validation>
              <v-container>
                <v-layout row="row" wrap="wrap" class="register">
                  <v-flex>
                    <p>Por favor ingresa los datos solicitados. Tu registro queda sujeto a aprobación.</p>
                  </v-flex>
                  <v-flex xs12 sm4>
                    <v-text-field
                      v-model="name.info"
                      :rules="name.rules"
                      label="Nombre"
                      clearable="clearable"
                      autocomplete="name"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm4>
                    <v-text-field
                      v-model="appat.info"
                      :rules="appat.rules"
                      label="Apellido Paterno"
                      clearable="clearable"
                      autocomplete="last-name"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm4>
                    <v-text-field
                      v-model="apmat.info"
                      :rules="apmat.rules"
                      label="Apellido Materno"
                      clearable="clearable"
                      autocomplete="last-name"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12>
                    <v-text-field
                      v-model="email.info"
                      :rules="email.rules"
                      label="Correo"
                      clearable="clearable"
                      autocomplete="new-email"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12="xs12" sm6="sm6">
                    <v-text-field
                      v-model="password.info"
                      :rules="password.rules"
                      label="Contraseña"
                      :type="'password'"
                      clearable="clearable"
                      autocomplete="new-password"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12="xs12" sm6="sm6">
                    <v-text-field
                      v-model="password_repeat.info"
                      :rules="password_repeat.rules"
                      label="Repite tu contraseña"
                      :type="'password'"
                      clearable="clearable"
                      autocomplete="new-password"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 d-flex>
                    <v-select
                      v-model="range.selected"
                      :hint="`${range.selected.state}, ${range.selected.abbr}`"
                      :items="range.hints"
                      item-text="state"
                      item-value="abbr"
                      persistent-hint
                      return-object
                      single-line
                      label="Rango"
                      required
                    ></v-select>
                  </v-flex>
                  <v-flex>
                    <v-checkbox
                      class="aceptar"
                      v-model="checkbox"
                      :rules="[v => !!v || 'Aceptar para continuar']"
                      required
                    >
                      <template v-slot:label>
                        <div class="in-label">
                          Estoy de acuerdo con el
                          <router-link to="/aviso">
                            <a>aviso de privacidad</a>
                          </router-link>.
                        </div>
                      </template>
                    </v-checkbox>
                  </v-flex>
                  <v-flex xs12 sm12>
                    <v-flex>
                      <div class="text-xs-center">
                        <v-btn
                          class="elevation-0"
                          id="enviar"
                          @click="submit"
                          color="primary"
                        >Enviar</v-btn>
                      </div>
                    </v-flex>
                    <br />
                    <v-flex xs12 sm12>
                      <div class="text-xs-center">
                        <p>
                          Ya tengo cuenta,
                          <router-link class="btn_login" to="/login">Iniciar sesión</router-link>
                        </p>
                      </div>
                    </v-flex>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-form>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>


<script>
import RegisterRequest from "./RegisterRequest";
import { mapMutations } from "vuex";
// Handle error messages from server if type is error

export default {
  name: "RegisterPage",
  data() {
    return {
      isValid: true,
      name: {
        info: '',
        rules: [
          v => !!v || "El nombre es requerido"
        ]
      },
      appat: {
        info: '',
        rules: [
          v => !!v || "El Apellido Paterno es requerido"
        ]
      },
      apmat: {
        info: '',
        rules: [
          v => !!v || "El Apellido Materno es requerido"
        ]
      },
      email: {
        info: "",
        rules: [
          v => !!v || "El correo es requerido",
          v => /.+@.+/.test(v) || "Ingresa un email válido"
        ]
      },
      password: {
        info: "",
        rules: [
          v => !!v || "La contraseña es requerida",
          v =>
            /^(?=.*\d).{4,12}$/.test(v) ||
            "Una contraseña debe al menos contener un número"
        ]
      },
      password_repeat: {
        info: "",
        rules: [
          v =>
            this.password.info === this.password_repeat.info ||
            "Las contraseñas deben coincidir"
        ]
      },
      range: {
        selected: { state: "Técnico en Atención Médica", abbr: "TAM" },
        hints: [
          { state: "Técnico en Atención Médica", abbr: "TAM" },
          { state: "Cadete en Prácticas", abbr: "CP" }
        ]
      },
      checkbox: false
    };
  },
  methods: {
    ...mapMutations({
      changeAlert: "CHANGE_ALERT",
      setLoading: "SET_LOADING"
    }),
    async submit() {
      if (this.$refs.register_form.validate() == false) {
        return;
      }

      this.setLoading({ appear: true, message: "Trabajando..." });
      let request = new RegisterRequest(
        this.email.info,
        this.password.info,
        this.range.selected.abbr,
        this.checkbox,
        this.name.info,
        this.appat.info,
        this.apmat.info
      );
      try {
        let response = await this.$http.post("/register", request.toJSON());
        if (response) {
          if (response.status == "ERROR") {
            this.errorHandler(response);
          } else {
            this.successRegistered();
          }
        }
      } catch (error) {
        this.errorHandler(error);
      } finally {
        this.setLoading({ appear: false, message: "" });
      }
    },
    errorHandler(error) {
      let errorMessage = error.response.data.message;
      this.changeAlert({
        appear: true,
        type: "info",
        message: errorMessage,
        timeout: 3000
      });
      this.email.info = "";
      this.password.info = "";
      this.password_repeat.info = "";
    },
    successRegistered() {
      this.setLoading({
        appear: true,
        message: "Trabajando..."
      });
      setTimeout( ()=> {
        this.setLoading({
          appear: true,
          message: "Registro exitoso."
        });
      }, 1000);
      setTimeout(() => {
        this.setLoading({ 
          appear: false,
          message: ""
        });
        this.$router.push("login");
      }, 3000);
    }
  }
};
</script>
<style>
.in-label {
  font-size: 14px;
}
.v-label {
  font-size: 14px;
}
input,
.v-select__selection {
  font-size: 14px !important;
}
.v-text-field {
  padding-top: 0 !important;
}
.register {
  padding: 20px;
}
.btn_login {
  text-align: center;
  color: rgb(17, 17, 17);
  text-decoration: underline;
}
</style>


